.const {
  min-height: 100vh;
  max-width: 75rem;
  margin: 0 auto; }
  .const__list {
    padding: 2rem;
    display: flex;
    flex-direction: column; }
    .const__list__item {
      display: flex;
      align-items: center;
      min-height: 20rem; }
      .const__list__item:not(:last-child) {
        border-bottom: var(--line);
        margin-bottom: 2rem; }
      .const__list__item .highlight__hover {
        background-image: linear-gradient(120deg, #ffc400c2 0%, #ffc400c2 100%);
        background-repeat: no-repeat;
        background-position: 0 88%;
        background-size: 100% 0;
        transition: background-size 0.25s ease-in; }
      .const__list__item:hover .highlight__hover {
        background-size: 100% 0.5rem; }
    .const__list__item--60 {
      flex: 0 0 60%; }
    .const__list__item--40 {
      flex: 0 0 40%; }
    .const__list__content {
      padding: 1rem 2rem;
      color: #141414; }
      -webkit-font-smoothing
.const__list__content--right {
        text-align: right; }
    .const__list__title {
      font-size: 2rem;
      text-transform: capitalize; }
    .const__list__paragraph {
      font-size: 1.2rem;
      font-weight: 400; }
      .const__list__paragraph:not(:last-child) {
        margin-bottom: .5rem; }
    .const__list__image-box {
      width: 100%;
      height: 20rem;
      border: 10px solid #ffc400c2;
      padding: 1rem; }
    .const__list__image {
      height: 100%;
      width: 100%;
      background-size: cover;
      background-position: 50% 50%; }
